<template>
<component v-bind:is="componentOrder[activeIndex]" v-on:next-step="toNextStep" v-bind:account-connector="accountConnector" ></component>
</template>

<script>

import RegisterMandatory from '@/components/RegisterMandatory.vue' 
import RegisterPersonalize from '@/components/RegisterPersonalize.vue'
import AccountRestConnector from '@/classes/AccountRestConnector.js'

export default {
  components: {
    'register-mandatory': RegisterMandatory ,
    'register-personalize': RegisterPersonalize
  },

  data() {
    return {
      accountConnector: null,
      componentOrder: ['register-mandatory', 'register-personalize'],
      activeIndex: 0
    }
  },

  created(){
    this.accountConnector = new AccountRestConnector();
  },

  methods: {
    toNextStep(){
      this.activeIndex ++;
    }
  }


}
</script>

<style scoped src="src/assets/css/login.css">
</style>


