import Home from '@/components/Home.vue'
import Menu from '@/components/Menu.vue'
import Shop from '@/components/Shop.vue'
import About from '@/components/About.vue'
import Login from '@/components/Login.vue'
import Register from '@/components/Register.vue'

export default [
    { path: '/', meta: {bodyClass: 'default-bg'}, component: Home },
    { path: '/menu', meta: {bodyClass: 'default-bg'}, component: Menu },
    { path: '/shop/:category', meta: {bodyClass: 'shop-bg'}, component: Shop},
    { path: '/info', meta: {bodyClass: 'about-bg'}, component: About},
    { path: '/login', meta: {bodyClass: 'shop-bg'}, component: Login},
    { path: '/register', meta: {bodyClass: 'shop-bg'}, component: Register},
]


