<template>
    <!-- Nav -->
    <nav>
      <div v-if="mobileMenuActive" class="menuActive" v-on:click="toggleMobileMenu()"> </div>
      <img src="@/assets/images/logo.png" class="logo" alt="TacoDel">
      <ul v-bind:class="{menuItems: true, menuActive:mobileMenuActive}">
        <li v-for="menuLink in menuLinks" v-bind:key="menuLink.name">
        <router-link class="underline-active" v-bind:to="menuLink.link" exact>{{ $t(menuLink.name) }}
            <span></span> <!-- For underline hover -->
        </router-link>

        </li>
      </ul>
        <span class="mobile-menu">
          <span class="primary-link-hover all-caps" style="padding-right: 20px;" v-on:click="switchLocale()">{{ this.locale.lang[this.locale.index] }}</span>
          <img src="@/assets/images/mobile-menu.png" class="clickable" v-on:click="toggleMobileMenu()" />
          <router-link to="/login">
            <img src="@/assets/images/login.png" />
            <span class="primary-link-hover">{{ $t('nav-bar.login') }}</span>
          </router-link>
        </span>
    </nav>
</template>

<script>
    export default {
    data () {
        return {
          locale: {
            index: 0,
            lang: ['nl', 'en'],
          },
          mobileMenuActive: false,
          menuLinks: [
            {
              name: 'nav-bar.offers',
              link: '/'
            },
            {
              name: 'nav-bar.menu',
              link: '/menu'
            },
            {
              name: 'nav-bar.contact',
              link: '/contact'
            },
            {
              name: 'nav-bar.about',
              link: '/info'
            }
          ]
        }
    },

    methods: {
      toggleMobileMenu(){
        this.mobileMenuActive = ! this.mobileMenuActive;
      },

      switchLocale(){
        this.locale.index ++;
        if(this.locale.index > this.locale.lang.length){
          this.locale.index = 0;
        }
        this.$i18n.locale = this.locale.lang[this.locale.index * -1 + 1];
      }
    },

    /** Close mobile menu when the route changes */
    watch: {
      $route() {
        this.mobileMenuActive = false;
      }
    }

  }
</script>

<style scoped src="src/assets/css/navbar.css">
</style>


