import Config from '@/classes/Restconfig'
import Axios from 'axios'

/**
 * Makes REST http-reqeuests to the Menu resource
 * Returns a promise to give the caller class more control
 * and requires less trust and better readability than using a callback.
 */
class MenuRestConnector {

    constructor(){
        this.baseUrl = Config.getUrl("menu");
    }


    /**
     * Gets all the products on them menu of the chosen category
     * @param {string} category category on the menu
     * @returns {Promise} promise of HTTP response
     */
    getMenuFromCategory(category){
        const promise = Axios
            .get(this.baseUrl + category)
            .catch(ex => {
                console.log(ex);
            });
        
        return promise;
    }

}

export default MenuRestConnector


