//@ts-check

/**
 * Counter used for the unique id in cart.
 * Not in class definition to keep it a private static variable
 */
let productCounter = 0;


/**
 * Representation of a product in the shopping cart
 */
 class ProductInCart {

    /**
     * 
     * @param {string} name 
     * @param {number} price 
     * @param {Array<Object>} choices 
     * @param {string} category
     */
    constructor(name, price, choices, category){
        productCounter ++;
        this.name = name;
        this.price = price;
        this.choices = choices;
        this.idInCart = productCounter;
        this.quantity = 1;
        this.category = category;
    }

    /**
     * Checks is two products are the same
     * and can be stacked in the shopping cart
     * @param {ProductInCart} productToCompare 
     * @returns true when the same, otherwise false
     */
    equals(productToCompare){
        return (productToCompare.name === this.name &&
        productToCompare.price === this.price &&
        productToCompare.choices.length === this.choices.length);
    }


}

export default ProductInCart


