import Vue from 'vue';

Vue.filter('to-euro', (value) => {
    return toPriceDipslay(value, true);
});

Vue.filter('to-price', (value) => {
    return toPriceDipslay(value, false);
});


/**
 * 
 * @param {number} value 
 * @param {boolean} [showSymbol] 
 */
function toPriceDipslay(value, showSymbol = false){
    value = value.toLocaleString('nl-NL', { style: 'currency', currency: 'EUR'});

    if(! showSymbol){
        value = value.slice(2);
    }

    return value;
}


