<template>
  <div class="content">
      <div class="grid">
          <section class="register">
            <div class="form-container">
              <h1>{{ $t('account.login') }} <span class="capitalize">{{ $t('account.with-email') }}</span></h1>
              <form action="localhost:8080/v1/account/login" method="POST">
              <!-- <div><span class="information">i <div>Please go on one line instead, that would be great</div></span><input type="email" placeholder="email" v-model="email" /></div> -->
                <input type="email" placeholder="email" v-model="form.email.value" v-bind:class="{error: !form.email.isValid}"/>
                <!-- <div class="error-description" v-if="!form.email.isValid"> {{ this.form.email.errors[0] }}</div> -->
                <input type="password" v-bind:placeholder="$t('account.password')" v-model="form.password.value" v-bind:class="{error: ! form.password.isValid}" />
                <!-- <div class="error-description" v-if="!form.password.isValid">{{ this.form.password.errors[0] }}</div> -->
                <button class="submit-form clickable" v-on:click.prevent="attemptLogin()">{{ $t('nav-bar.login') }}</button>
                <!-- <span class="error-description" style="display:block; text-align:center; margin-top: 16px">Invalid email username combination</span> -->
              </form>
              <p>{{ $t('account.no-account') }} <span class="primary-link-hover"><router-link to="/register">{{ $t('account.sign-up') }}</router-link></span></p>
            </div>
          </section>
      </div>
  </div>
</template>

<script>
import AccountRestConnector from 'src/classes/AccountRestConnector.js'
import {isEmail, isFilledIn} from '@/classes/FormValidation'
import FormHelper from '@/classes/FormHelper'
import FormField from '@/classes/FormField'

export default {
  data(){
    return{
      form: {
        email: new FormField(isEmail()),
        password: new FormField(isFilledIn())
      },

      accountConnector: null,
      formHelper: null,
    }

  },

  created(){
    this.accountConnector = new AccountRestConnector();
    this.formHelper = new FormHelper(this.form);
  },

  methods: {
    attemptLogin(){
      this.formHelper.validateForm();
    }
  }
}
</script>

<style scoped src="src/assets/css/login.css">
</style>


