import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueBodyClass from 'vue-body-class'
import Routes from '@/assets/plugins/routes'
import i18n from '@/assets/plugins/i18n'
import '@/assets/plugins/filters'

Vue.use(VueRouter);


let routerBase = (process.env.NODE_ENV === 'production') ? '/tacodel-deploy/' : '/';

const myRouter = new VueRouter({
    routes: Routes,
    mode: 'hash',
    base: routerBase
});

const vueBodyClass = new VueBodyClass(Routes);
myRouter.beforeEach((to, from, next) => { vueBodyClass.guard(to, next) });

new Vue({
    el: '#app',
    router: myRouter,
    i18n,
    render: h => h(App),
})





