<template>
  <div class="content">
      <div class="grid">
          <section class="register">
            <div class="form-container">
              <h1>{{ $t('account.personalize') }} <span>{{ $t('account.your-account') }}</span></h1>
              <form action="">
                <input type="text" v-bind:placeholder="$t('account.last-name')" class="error" />
                <input type="text" v-bind:placeholder="$t('account.zip-code')" />
                <input type="text" v-bind:placeholder="$t('account.street-and-house')" />
                <button class="submit-form clickable">{{ $t('account.personalize') }}</button>
              </form>
              <p><span class="primary-link-hover capitalize-block"> <router-link to="/login">{{ $t('account.skip') }}</router-link></span></p>
            </div>
          </section>
      </div>
  </div>
</template>

<script>
export default {

}
</script>

<style scoped src="src/assets/css/login.css">
</style>


