<template>
  <div class="content">
      <div class="grid">
          <section class="register">
            <div class="form-container">
              <h1>{{ $t('account.register') }} <span>{{ $t('account.your-account') }}</span></h1>
              <form action="">
                <input type="text" v-bind:placeholder="$t('account.first-name')" v-model="form.firstName.value" v-bind:class="{error: ! form.firstName.isValid}" />
                <input type="email" placeholder="email" v-model="form.email.value" v-bind:class="{error: ! form.email.isValid}" />
                <input type="email" v-bind:placeholder="$t('account.repeat') + ' email'" v-model="form.repeatEmail.value"  v-bind:class="{error: ! form.repeatEmail.isValid}"/>
                <input type="password" v-bind:placeholder="$t('account.password')" v-model="form.password.value" v-bind:class="{error: ! form.password.isValid}" />
                <button class="submit-form clickable" v-on:click.prevent="toNextStep">{{ $t('account.sign-up') }}</button>
              </form>
              <p> {{ $t('account.own-an-account') }} <span class="primary-link-hover"> <router-link to="/login"> {{ $t('nav-bar.login') }}</router-link></span></p>
            </div>
          </section>
      </div>
  </div>
</template>

<script>
import {isEmail, isFilledIn, isSameAs} from '@/classes/FormValidation'
import FormHelper from '@/classes/FormHelper'
import FormField from '@/classes/FormField'


export default {
  props: {
    accountConnector: {
      type: Object,
      required: true
    }
  },

  data(){
    return {
      form: {
        firstName: new FormField(isFilledIn()),
        email: new FormField(isEmail()),
        repeatEmail: new FormField(isEmail(), isSameAs("email")),
        password: new FormField(isFilledIn())
      },

      formHelper: null
    }
  },

  computed: {
    email(){
      return this.form.email;
    }
  },

  created() {
    this.formHelper = new FormHelper(this.form);
  },

  methods: {
    toNextStep(){
      this.formHelper.validateForm();
      this.accountConnector.signUp(this.firstName, this.email, this.password);
      console.log(this.accountConnector);
      // this.$emit('next-step');
    }
  }

}
</script>

<style scoped src="src/assets/css/login.css">
</style>


