/**
 * Seperate file for the REST configuration.
 * This allows usability in multiple classes 
 * and differentiates between development and production
 */
class RestConfig {
    /**
     * 
     * @param {String} protocol default http
     * @param {String} host default localhost
     * @param {String} version default v1
     * @param {String} port default 8080
     */
    constructor(protocol= "http", host = "localhost", version = "v1", port = "8080"){
        this.protocol = protocol;
        this.host = host;
        this.version = version;
        this.port = port;

        if(port !== ''){
            this.port = ':' + port;
        }
    }

    /**
     * @param {String} resource the resource of the endpoint 
     */
    getUrl(resource){
        return `${this.protocol}://${this.host}${this.port}/${this.version}/${resource}/`;
    }
}

let restConfig;


if (process.env.NODE_ENV === 'production'){
    restConfig = new RestConfig(
        'https',
        'tacodel-rest.herokuapp.com',
        'v1',
        '');
}
else{
    restConfig = new RestConfig(
        'http',
        'localhost',
        'v1',
        '9000');
}

export default restConfig;




