<template>
<!-- content -->
<div class="content">
      <div class="grid">
        <div class="buy-method">{{ $t('home.delivery') }}<img src="@/assets/images/delivery-icon.png" /></div>
        <div class="buy-method">{{ $t('home.to-go') }}<img src="@/assets/images/togo-icon.png" /></div>
        
        <!-- offers -->
        <div v-for="offer in offers" v-bind:key="offer.id" class="offer">
          <img v-bind:src="require(`@/assets/images/${offer.img}`)" />
          <div class="price">
            <img src="@/assets/images/speech-bubble.png"/>
            <span>{{ offer.price | to-price }}</span>
          </div>
        </div>
        <!-- end offers -->

      </div>
      <!-- end grid -->
      </div>
      <!-- end content -->
</template>

<script>

export default {
  data () {
    return {
      offers : [
        {
          id: 1,
          img: "offer-one.png",
          price: 5
        },
        {
          id: 2,
          img: "offer-two.png",
          price: 7.50
        }
      ]
    }
  }

}
</script>

<style scoped src="src/assets/css/home.css">
</style>






