<template>
<div class="content">
    <div class="grid">
        <h1 class="title"> {{ $t(`menu.categories.${category}`) }} </h1>
        <div class="back-button clickable"><router-link class="full-size-link" to="/menu"></router-link>{{ $t('shop.back') }}</div>

        <section class="position-controller">

            <aside class="product-wrapper">
                <product-in-shop v-for="product in products" 
                                 v-bind:key="product.name" 
                                 v-bind:product="product" 
                                 v-on:add-product="addToCart">
                </product-in-shop>
             </aside>

        </section>
        <!-- end product -->

        <shopping-cart ref="shoppingCart"></shopping-cart>

    </div>
</div>
</template>

<script>
import ShoppingCart from 'src/components/ShoppingCart.vue'
import ProductInCart from 'src/classes/ProductInCart'
import ProductInShop from 'src/components/ProductInShop.vue'
import MenuRestConnector from 'src/classes/MenuRestConnector.js'
import Axios from 'axios'

export default {
    
    components: {
        'shopping-cart' : ShoppingCart,
        'product-in-shop' : ProductInShop
    },

    data(){
        return{
            category: this.$route.params.category,
            menuConnector: null,
            products: []
        }
    },

    /**
     * Load the menu on creation based on the category
     */
    created(){
        this.menuConnector = new MenuRestConnector();
        const promise = this.menuConnector.getMenuFromCategory(this.category);
        promise.then(response => this.products = response.data);
    },

    methods: {
        addToCart(product){
            this.$refs.shoppingCart.addProduct(product);
        }
    }
}
</script>

<style lang="css" src="src/assets/css/shop.css" scoped>
</style>


