<template>
 <!-- Basket -->
      <section class="basket">

        <h1>{{ $t('shopping-cart.order') }}</h1>

        <section class="basket-items">

          <p v-if="products.length === 0" style="text-align: center;">{{ $t('shopping-cart.empty') }}</p>
          <article v-for="category in categorizedCart" v-bind:key="category.name">
            <h1 class="category-name">{{ category.name }}</h1>
            <ul>
              <li v-for="product in category.products" v-bind:key="product.idInCart">
                  <span class="before-price"> {{ product.quantity }}x {{ product.name }}</span>
                  <span class="product-price">{{ product.price | to-euro }}</span>
                  <div class="button clickable" v-on:click="removeProduct(product.idInCart)">X</div>
              </li>
            </ul>
          </article>

      </section>

        <h1 class="total-price">{{ $t('shopping-cart.total') }} {{ totalPrice | to-euro }}</h1>

      </section>
      <!-- End of basket -->
</template>

<script type="text/javascript">
import LocalStorageUtil from 'src/classes/LocalStorageUtil'

    export default {

    data () {
        return {
            /** Key used in local storage */
            key: 'shoppingCart',
            products: []
        }
    },

    /** Load potential existing cart from localstorage */
    created(){
        this.localStorageUtil = new LocalStorageUtil();
        this.products = this.localStorageUtil.loadFromStorage(this.key, true) || [];
    },

    methods:{

        addProduct(newProduct){
            const sameProduct = this.products.find(product => newProduct.equals(product));
            if(sameProduct != null){
                sameProduct.quantity ++;
            }
            else{
                this.products.push(newProduct);
            }

            this.localStorageUtil.putInStorage(this.key, this.products, true);
        },

        /**
         * @param {number} - id of product to remove
         */
        removeProduct(idInCart){
            const indexOfItem = this.products.findIndex(product => product.idInCart === idInCart);

            if(this.products[indexOfItem].quantity === 1){
                this.products.splice(indexOfItem, 1);
            }
            else{
                this.products[indexOfItem].quantity --;
            }

            this.localStorageUtil.putInStorage(this.key, this.products, true);
        }
    },

    computed:{
        /** 
         * @returns {number} The total amount of all products in the cart
         */
        totalPrice(){
            return this.products.reduce((total, item) => total + (item['price'] * item['quantity']), 0);
        },

        /**
         * @returns {Array<Object>} returns a list of categories with their products
         */
        categorizedCart(){
            if(this.products.length === 0)
                return [];

            const output = [];
            const sorted = this.sortedProducts
            
            let previous = sorted[0].category;
            let toPush = {name: previous, products: []};
            sorted.forEach( (item) => {
                if(item.category != previous){
                    previous = item.category;
                    output.push(toPush);
                    toPush = {name: item.category, products: []};
                }
                toPush.products.push(item);
            });

            output.push(toPush);
            return output;
        },

        /** 
         * Called to sort products in categorized cart
         */
        sortedProducts(){
           const sorted = this.products.sort((a,b) => {
                if(a.category > b.category)
                    return -1;
                if(a.category < b.category)
                    return 1;
                return 0;
            });

            return sorted;
        }
    }

    }
</script>

<style scoped src="src/assets/css/shoppingCart.css">
</style>


