//@ts-check

const keyNames = {
    shoppingCart: 'SHOPPING_CART'
};

/**
 * @param {string} key 
 * @throws if the key does not exist
 * @returns {string} the string representation of the key
 */
function getKeyValue(key){
    const toReturn = keyNames[key];
    if(toReturn === undefined)
        throw key + ' is not a valid key name. Please check your spelling';
    return toReturn;
}

/**
 * Class for reading and writing to local strorage
 * Seperated into one class for json handling
 * and being able to mock calls to local storage.
 * @typedef {LocalStorageUtil} _localStorageUtil
 */
class LocalStorageUtil{

    constructor(){
        // empty constructor for readability of class
    }

    /**
     * 
     * @param {string} key 
     * @param {boolean} [isJson] - is the object being placed in local storage a json object
     */
    loadFromStorage(key, isJson = false){
        let toReturn = this._loadFromStorage(getKeyValue(key));
        
        if(isJson){
            toReturn = JSON.parse(toReturn);
        }

        return toReturn;
    }


    /**
     * 
     * @param {string} key 
     * @param {object} value 
     * @param {boolean} [isJson] - is the object in local storage a json object
     */
    putInStorage(key, value, isJson = false){
        if(isJson){
            value = JSON.stringify(value);
        }

        this._putInStorage(getKeyValue(key), value);
    }

    /**
     * allows putting items in local storage to get mocked
     * and to test the putInStorage method
     * @param {string} key 
     * @param {string} value 
     */
    _putInStorage(key, value){
        localStorage.setItem(key, value);
    }

    /**
     * allows getting from local storage to get mocked
     * and to test the loadFromStorage Method
     * @param {string} key 
     */
    _loadFromStorage(key){
        return localStorage.getItem(key);
    }

}

export default LocalStorageUtil


