import Config from '@/classes/Restconfig'
import Axios from 'axios'

/**
 * Makes REST http-request to account resource
 * Returns a promise to give the caller class more control
 * and requires less trust and better readability than using a callback.
 */
class AccountRestConnector {

    constructor(){
        this.baseUrl = Config.getUrl("account");
    }


    /**
     * Gets all the products on them menu of the chosen category
     * @param {string} email the email used to login
     * @param {string} password the password used to login
     * @returns {Promise} promise of HTTP response
     */
    attemptLogin(email, password){
        const endpoint = this.baseUrl + "login";
        const data = {
            email: email,
            password: password
        };

        const promise = Axios
            .post(endpoint, data)
            .catch(ex => {
                console.log(ex);
            });
        
        return promise;
    }

    signUp(firstName, email, password){
        const endpoint = this.baseUrl + "register"
        const data = {
            firstName: firstName,
            email: email,
            password: password
        };

        const promise = Axios.post(endpoint, data).catch(ex => {
            console.log(ex);
        })

        return promise;
    }

}

export default AccountRestConnector


