<template>
<div class="content">
  <div class="grid"> 

  <div class="category-wrapper">

      <section class="category" v-for="category in categories" v-bind:key="category.name">
        <router-link  v-bind:to="/shop/ + category.route" class="full-size-link" exact></router-link>
          <div class="image-border"><img v-bind:src="require(`@/assets/images/${category.img}`)"/></div>
          <span class="category-text"><h1>{{ $t(category.name) }}</h1></span>
      </section>

  </div>

   <shopping-cart></shopping-cart>
  </div>
</div>
</template>

<script>
import ShoppingCart from 'src/components/ShoppingCart.vue'

export default {

  components: {
    'shopping-cart' : ShoppingCart
  },

  data () {
    return {
      categories: [
        {
          name:'menu.categories.menus',
          img: "category-desktop-meal.png",
          route: "menus"
        },
        {
          name:'menu.categories.tacos',
          img: "category-desktop-taco.png",
          route: "tacos"
        },
        {
          name:'menu.categories.sides',
          img: "category-desktop-sides.png",
          route: "sides"
        },
        {
          name:'menu.categories.drinks',
          img: "category-desktop-drinks.png",
          route: "drinks"
        }
      ]
    }
  }

}
</script>


<style scoped src="src/assets/css/menu.css">
</style>



