<template>
        <!-- Product -->
    <article class="product">
        <article class="product-info">
            <img v-bind:src="product.image" />
            <span class="product-name">{{ product.name }}</span>
            <span class="product-price">{{ totalPrice | to-euro }}</span>
        </article>

        <!-- Options -->
        <article class="product-options">
            <div class="add-button clickable" v-on:click="addToCart()">{{ $t('shop.add-to-cart') }}</div>
            <select class="dropdown" v-for="modification in product.modifications" v-bind:key="modification.name" v-model="modification.selectedIndex">
                <option v-for="(choice,index) in modification.choices" v-bind:key="choice.name" v-bind:value="index">{{ choice.name }}</option>
            </select>
        </article>

    </article>
    <!-- End product -->
</template>

<script>

import ProductInCart from 'src/classes/ProductInCart'

export default {
    data(){
        return{
        }
    },

    methods: {

        addToCart(){
            const productInCart = this.createProductInCart();
            this.$emit('add-product', productInCart);
        },
        
        /** Transform into a product in cart so it can be added to the shopping cart */
        createProductInCart(){
            return new ProductInCart(this.fullName, this.totalPrice, this.selectedChoices, this.product.category);
        }
    },

    props: {
        product: {
            type: Object,
            required: true
        }
    },

    computed: {
        /**
         * @returns {number} with price of selected choices
         */
        totalPrice(){
            const modificationTotal = this.product.modifications.reduce( (total, modification) => {
                return total + modification.choices[modification.selectedIndex].price;
            }, 0);
            return this.product.basePrice + modificationTotal;
        },

        /**
         * @returns {string} with prefix name of selected choices
         */
        fullName(){
            const modificationNames = this.product.modifications.reduce( (total, modification) => {
                return total + modification.choices[modification.selectedIndex].productPrefix;
            }, "");

            return modificationNames + this.product.name;
        },
        
        /**
         * @returns {Array} of currently selected choices
         */
        selectedChoices(){
            return this.product.modifications.map( (modification) => {
                return modification.choices[modification.selectedIndex];
            });
        }
    }
}
</script>

<style lang="css" src="src/assets/css/productInShop.css" scoped>
</style>


